/*Обнуление*/
* {
	padding: 0;
	margin: 0;
	border: 0;
}

*,
*:after,
*:before {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

:active,
:focus {
	outline: none;
}

a:active,
a:focus {
	outline: none;
}

aside,
footer,
header,
nav {
	display: block;
}

body,
html {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

button,
input,
textarea {
	font-family: inherit;
}

input::-ms-clear {
	display: none;
}

button {
	cursor: pointer;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

a,
a:visited {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: 400;
}

body,
html {
	height: 100%;
}

body {
	font-family: 'Montserrat', sans-serif, Arial, sans-serif;
	box-sizing: border-box;
  background-color: ivory;
	font-size: 17px;
	line-height: 24px;
}

.wrapper{
	display: flex;
	min-height: 100%;
	overflow: hidden;
	flex-direction: column;

}
